import { getFilteredHygineScore } from "../utils";

const getHygineRatingPercentOptions = (selectedAuthority, hygineScoreAll) => {
  // get array of [ratingType, rating %] to set to pie chart
  // [5, 65], [4, 15], [3, 10] etc
  const hygineRatingPercent = getFilteredHygineScore(hygineScoreAll).map(
    (rating) => {
      if (rating.value !== 0) {
        return [rating.title, rating.value];
      }

      return [];
    }
  );

  return {
    chart: {
      plotBackgroundColor: null,
      plotBorderWidth: null,
      plotShadow: false,
      type: "pie"
    },
    title: {
      text: "Hygine rating <br> for <br>" + selectedAuthority,
      align: "center",
      verticalAlign: "middle",
      y: 60
    },
    tooltip: {
      pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>"
    },
    accessibility: {
      point: {
        valueSuffix: "%"
      }
    },
    plotOptions: {
      pie: {
        allowPointSelect: true,
        cursor: "pointer",
        dataLabels: {
          enabled: true,
          format: "<b>{point.name}</b>: {point.percentage:.1f} %"
        }
      }
    },
    series: [
      {
        type: "pie",
        name: "Estiblishments",
        colorByPoint: true,
        data: hygineRatingPercent
      }
    ]
  };
};

export { getHygineRatingPercentOptions };
