const getAvg = (obj) => {
  let sum = 0;
  let estiblishmentWithRating = 0;
  for (const [key, value] of Object.entries(obj)) {
    sum = sum + key * value;
    estiblishmentWithRating = estiblishmentWithRating + value;
  }

  return (sum / estiblishmentWithRating).toFixed(4);
};

export { getFilteredHygineScore, getAvg };
