import mapData from "./gb-map";
import { getAvg } from "../utils";

const getUKMapOptions = (data) => {
  const englandData = getAvg(data[0].ratings);
  const nIrelandData = getAvg(data[1].ratings);
  const walesData = getAvg(data[2].ratings);

  return {
    chart: {
      map: mapData,
      height: 600
    },

    title: {
      text: ""
    },

    subtitle: {
      text:
        'Source map: <a href="http://code.highcharts.com/mapdata/countries/gb/custom/gb-countries.js">United Kingdom countries</a>'
    },

    mapNavigation: {
      enabled: true,
      buttonOptions: {
        verticalAlign: "bottom"
      }
    },

    series: [
      {
        data: [
          ["gb-eng", englandData],
          ["gb-wls", walesData],
          ["gb-nir", nIrelandData]
        ],
        name: "Average Hygiene Rating",
        states: {
          hover: {
            color: "#BADA55"
          }
        },
        dataLabels: {
          enabled: true,
          format: "{point.name} </br> {point.value}"
        }
      }
    ]
  };
};

export { getUKMapOptions };
