import { getAvg } from "../utils";

const getAverageRatingMeter = (establishments = [], selectedAuthority) => {
  const ratingObj = { 5: 0, 4: 0, 3: 0, 2: 0, 1: 0, 0: 0 };

  // iterate over all establishments and populate ratingObj values
  // eg. {5: 100, 4: 30, 3: 20 etc}
  establishments.forEach((establishment) => {
    if (establishment.RatingValue >= 0) {
      ++ratingObj[establishment.RatingValue];
    }
  });

  // get average rating
  const averageRating = establishments.length
    ? parseFloat(getAvg(ratingObj))
    : 0;

  return {
    chart: {
      type: "gauge",
      plotBorderWidth: 1,
      plotBackgroundImage: null,
      height: 400
    },
    title: {
      text: ""
    },
    pane: [
      {
        startAngle: -45,
        endAngle: 45,
        background: null,
        center: ["50%", "80%"],
        size: 400
      }
    ],
    tooltip: {
      enabled: false
    },
    yAxis: [
      {
        min: 0,
        max: 5,
        tickInterval: 1,
        labels: {
          distance: 20,
          step: 1
        },
        plotBands: [
          {
            from: 0,
            to: 3,
            color: "black"
          },
          {
            from: 3,
            to: 4.5,
            color: "#C02316"
          },
          {
            from: 4.5,
            to: 5,
            color: "yellow"
          }
        ],
        pane: 0,
        title: {
          text:
            '<b><span style="font-size:18px">Avg. Rating: ' +
            (!isNaN(averageRating) ? averageRating : "<br>Not Applicable") +
            "</span></b>",
          y: -40
        }
      }
    ],

    plotOptions: {
      gauge: {
        dataLabels: {
          enabled: false
        },
        dial: {
          radius: "100%"
        }
      }
    },
    series: [
      {
        name: selectedAuthority,
        data: [averageRating],
        yAxis: 0
      }
    ]
  };
};

export { getAverageRatingMeter };
