import React from "react";
import Highcharts from "highcharts";
import highchartsMore from "highcharts/highcharts-more";
import HighchartsReact from "highcharts-react-official";
import axios from "axios";
import {
  Card,
  CardContent,
  Typography,
  Grid,
  Paper,
  TextField,
  CircularProgress
} from "@material-ui/core";
import { Autocomplete } from "@material-ui/lab";

// Chart components
import EstablishmentList from "./establishmentList";
import { getHygineRatingPercentOptions } from "./hygineRatingPercentChart";
import { getBusinessPercentChart } from "./businessTypeDrillDownChart";
import { getAverageRatingMeter } from "./averageRatingMeter";

//highcharts
require("highcharts/modules/exporting")(Highcharts);
require("highcharts/modules/drilldown")(Highcharts);
highchartsMore(Highcharts);
require("highcharts/modules/solid-gauge")(Highcharts);

const headers = { "x-api-version": 2 };

class RatingByAuthority extends React.Component {
  // initial state
  state = {
    isLoading: false,
    authorities: [], // list of authorities
    selectedAuthority: "", //slected authority
    establishments: [], // list of estiblishment in authority
    hygineScoreAll: [], // array of all hygine rating in selected authority
    businessTypes: {} // list of business type in selected authority
  };

  // on page load
  async componentDidMount() {
    this.chart = this.refs.chart && this.refs.chart.chart;
    const authoritiesData = [];

    // get list of authority
    const authoritiesResponse = await axios.get(
      "https://api.ratings.food.gov.uk/authorities/basic/",
      { headers }
    );

    // transform authority response for dropdown label and value
    authoritiesResponse.data.authorities.forEach((authority) => {
      authoritiesData.push({
        value: authority.LocalAuthorityId,
        label: authority.Name
      });
    });

    this.setState({
      authorities: authoritiesData
    });
  }

  // selected authority selection event
  handleChange = async (e, selectedAuthority) => {
    //set selected authority, loading indicator
    this.setState({
      selectedAuthority: selectedAuthority.label,
      isLoading: true
    });

    const authorityId = selectedAuthority.value;

    // get list of establishments in selected authority
    let pageNumber = 1;
    let establishmentsResponse = await axios.get(
      "https://api.ratings.food.gov.uk/establishments",
      {
        headers,
        params: {
          localAuthorityId: authorityId,
          pageNumber,
          pageSize: 5000
        }
      }
    );

    let establishmentsData = establishmentsResponse.data.establishments;

    /*
      if records greater than 5000,
      iterate and send another api call for next set of results
      till all list is fetched
    */
    while (establishmentsResponse.data.meta.totalPages !== pageNumber) {
      ++pageNumber;
      const nextResponse = await axios.get(
        "https://api.ratings.food.gov.uk/establishments",
        {
          headers,
          params: {
            localAuthorityId: authorityId,
            pageNumber,
            pageSize: 10000
          }
        }
      );

      // append new list to previous results
      establishmentsData = [
        ...establishmentsData,
        ...nextResponse.data.establishments
      ];
    }

    const businessTypesObj = {};
    const totalHygineScore = [];

    // iterate over establishments and set business type obj
    // for different types of businesses in that authority
    establishmentsData.forEach((establishment) => {
      if (businessTypesObj[establishment.BusinessType]) {
        businessTypesObj[establishment.BusinessType] += 1;
      } else {
        businessTypesObj[establishment.BusinessType] = 1;
      }

      // push rating of establishment in totalHygineScore array
      // for calculating average rating
      totalHygineScore.push(establishment.RatingValue);
    });

    // after fetching all establishment,
    // getting hygine score arr and business types
    // update component state and loading to false
    this.setState({
      establishments: establishmentsData.map((establishment) => ({
        ...establishment,
        id: establishment.FHRSID
      })),
      hygineScoreAll: totalHygineScore,
      businessTypes: businessTypesObj,
      isLoading: false
    });
  };

  render() {
    const {
      authorities,
      selectedAuthority,
      establishments,
      hygineScoreAll,
      businessTypes,
      isLoading
    } = this.state;

    //get hygine rating distribution pie chart options
    const hygineRatingPercentChart = getHygineRatingPercentOptions(
      selectedAuthority,
      hygineScoreAll
    );

    // get business types bar chart options
    const businessPercentChart = getBusinessPercentChart(
      selectedAuthority,
      establishments,
      businessTypes
    );

    // get gauge meter average rating options
    const averageRatingMeter = getAverageRatingMeter(
      establishments,
      selectedAuthority
    );

    return (
      <div>
        <Autocomplete
          id="combo-box-demo"
          options={authorities}
          disableClearable
          getOptionLabel={(option) => option.label}
          style={{ width: 300, margin: "30px 0" }}
          renderInput={(params) => (
            <TextField
              {...params}
              label="Select Food Authority"
              variant="outlined"
            />
          )}
          onChange={this.handleChange}
        />
        {isLoading ? (
          <CircularProgress />
        ) : (
          <div>
            <Typography variant="h5" color="textPrimary" gutterBottom>
              <b>Total establishments in {selectedAuthority}: </b>
              {establishments.length}
            </Typography>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Paper>
                  <Card style={{ backgroundColor: "grey" }}>
                    <CardContent>
                      <Typography variant="h5" color="textPrimary" gutterBottom>
                        <b>Business Type Distribution</b>
                      </Typography>
                      <HighchartsReact
                        highcharts={Highcharts}
                        constructorType={"chart"}
                        options={businessPercentChart}
                        ref={"chart"}
                      />
                    </CardContent>
                  </Card>
                </Paper>
              </Grid>
              <Grid item xs={6}>
                <Card style={{ backgroundColor: "grey" }}>
                  <CardContent>
                    <Typography variant="h5" color="textPrimary" gutterBottom>
                      <b>Hygiene Rating Distribution</b>
                    </Typography>
                    <HighchartsReact
                      highcharts={Highcharts}
                      constructorType={"chart"}
                      options={hygineRatingPercentChart}
                      ref={"chart"}
                    />
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Paper>
                  <Card style={{ backgroundColor: "grey" }}>
                    <CardContent>
                      <Typography variant="h5" color="textPrimary" gutterBottom>
                        <b>Average hygiene rating</b>
                      </Typography>
                      <HighchartsReact
                        highcharts={Highcharts}
                        constructorType={"chart"}
                        options={averageRatingMeter}
                        ref={"chart"}
                      />
                    </CardContent>
                  </Card>
                </Paper>
              </Grid>

              <Grid item xs={12}>
                <Paper>
                  <Card>
                    <CardContent>
                      <Typography variant="h5" color="textPrimary" gutterBottom>
                        <b>List of Establishments in {selectedAuthority}</b>
                      </Typography>
                      <EstablishmentList establishments={establishments} />
                    </CardContent>
                  </Card>
                </Paper>
              </Grid>
            </Grid>
          </div>
        )}
      </div>
    );
  }
}

export default RatingByAuthority;
