const getCountryComparisonChartOptions = (data) => {
  const englandData = data[0];
  const nIrelandData = data[1];
  const walesData = data[2];

  return {
    chart: {
      type: "column"
    },
    title: {
      text: ""
    },
    subtitle: {
      text: "data-source: ratings.food.gov.uk"
    },
    xAxis: {
      categories: ["5", "4", "3", "2", "1", "0"],
      crosshair: true
    },
    yAxis: {
      min: 0,
      title: {
        text: "% of Estiblishments"
      }
    },
    tooltip: {
      headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
      pointFormat:
        '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
        '<td style="padding:0"><b>{point.y:.1f} %</b></td></tr>',
      footerFormat: "</table>",
      shared: true,
      useHTML: true
    },
    plotOptions: {
      column: {
        pointPadding: 0.2,
        borderWidth: 0
      }
    },
    series: [
      {
        name: "England",
        data: [
          (englandData.ratings[5] / englandData.totalEstiblishments) * 100,
          (englandData.ratings[4] / englandData.totalEstiblishments) * 100,
          (englandData.ratings[3] / englandData.totalEstiblishments) * 100,
          (englandData.ratings[2] / englandData.totalEstiblishments) * 100,
          (englandData.ratings[1] / englandData.totalEstiblishments) * 100,
          (englandData.ratings[0] / englandData.totalEstiblishments) * 100
        ]
      },
      {
        name: "N. Ireland",
        data: [
          (nIrelandData.ratings[5] / nIrelandData.totalEstiblishments) * 100,
          (nIrelandData.ratings[4] / nIrelandData.totalEstiblishments) * 100,
          (nIrelandData.ratings[3] / nIrelandData.totalEstiblishments) * 100,
          (nIrelandData.ratings[2] / nIrelandData.totalEstiblishments) * 100,
          (nIrelandData.ratings[1] / nIrelandData.totalEstiblishments) * 100,
          (nIrelandData.ratings[0] / nIrelandData.totalEstiblishments) * 100
        ]
      },
      {
        name: "Wales",
        data: [
          (walesData.ratings[5] / walesData.totalEstiblishments) * 100,
          (walesData.ratings[4] / walesData.totalEstiblishments) * 100,
          (walesData.ratings[3] / walesData.totalEstiblishments) * 100,
          (walesData.ratings[2] / walesData.totalEstiblishments) * 100,
          (walesData.ratings[1] / walesData.totalEstiblishments) * 100,
          (walesData.ratings[0] / walesData.totalEstiblishments) * 100
        ]
      }
    ]
  };
};

export { getCountryComparisonChartOptions };
