import { getFilteredHygineScore } from "../utils";

const getBusinessPercentChart = (
  selectedAuthority,
  establishments,
  businessTypes = {}
) => {
  const businessTypesFormattedData = [];
  const hygineRatingByBusinessTypes = [];
  for (const [key, value] of Object.entries(businessTypes)) {
    // iterate over businessTypes and transform
    // eg. [
    // {name: 'business type 1', y: 10%, drilldown: 'business type 1'}
    // {name: 'business type 2', y: 20%, drilldown: 'business type 2'}
    // ]
    businessTypesFormattedData.push({
      name: key,
      y: Math.round((value / establishments.length) * 100),
      drilldown: key // key for inner detail chart
    });

    const businessTypeRating = [];

    // iterate over establishments and get rating by businessType key
    // eg. businessTypeRating = [5,5,4,3,5,5,3,...]
    establishments.forEach((obj) => {
      if (obj.BusinessType === key) {
        businessTypeRating.push(obj.RatingValue);
      }
    });

    // get array of [ratingType, rating %] for detailed bar chart for each business type
    // [5, 65], [4, 15], [3, 10] etc
    const hygineRatingPercent = getFilteredHygineScore(businessTypeRating).map(
      (rating) => {
        if (rating.value !== 0) {
          return [rating.title, rating.value];
        }

        return [];
      }
    );

    hygineRatingByBusinessTypes.push({
      name: key,
      id: key,
      data: hygineRatingPercent
    });
  }

  return {
    chart: {
      type: "column"
    },
    title: {
      text: "Types of Businesses in " + selectedAuthority
    },
    subtitle: {
      text:
        "Click the slices to view further breakdown of hygine rating for based on type of food estiblishment"
    },
    accessibility: {
      announceNewData: {
        enabled: true
      }
    },
    xAxis: {
      type: "category",
      labels: {
        rotation: -10
      }
    },
    yAxis: {
      title: {
        text: "Percentage of Establishments"
      }
    },
    legend: {
      enabled: false
    },
    plotOptions: {
      series: {
        borderWidth: 0,
        dataLabels: {
          enabled: true,
          format: "{point.y:.1f}%"
        }
      }
    },

    tooltip: {
      headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
      pointFormat:
        '<span style="color:{point.color}">{point.name}</span>: <b>{point.y:.2f}%</b>'
    },

    series: [
      {
        name: "Business Type",
        colorByPoint: true,
        data: businessTypesFormattedData
      }
    ],
    drilldown: {
      series: hygineRatingByBusinessTypes
    }
  };
};

export { getBusinessPercentChart };
